/* eslint-disable default-case */
// @flow

import React, { Component } from "react";
import type { Node } from "react";
import "./DebuggingDemo.css";
import classNames from "classnames";

type DemoType =
  | "no_offset"
  | "hidden_ancestor"
  | "same_height"
  | "unsupported_element"
  | "working";

type Props = {|
  demo: DemoType
|};

export default class DebuggingDemo extends Component<Props> {
  render(): Node {
    const demo = this.props.demo;
    return (
      <div className={`DebuggingDemo DebuggingDemo-${demo}`}>
        <div
          className={classNames({
            "DebuggingDemo-Left": true,
            StickyDebug: demo === "same_height"
          })}
        >
          <div
            className={classNames({
              "DebuggingDemo-Inputs": true,
              StickyDebug: demo !== "same_height"
            })}
          >
            <p>What is your name?</p>
            <p>What is your quest?</p>
            <p>What is the air-speed velocity of an unladen swallow?</p>
          </div>
        </div>
        <div className="DebuggingDemo-Right">
          <div className="DebuggingDemo-Image" />
          <div className="DebuggingDemo-Image" />
          <div className="DebuggingDemo-Image" />
          <div className="DebuggingDemo-Image" />
          <div className="DebuggingDemo-Image" />
          <div className="DebuggingDemo-Image" />
        </div>
      </div>
    );
  }
}
