/* eslint-disable default-case */
// @flow

import React, { Component } from "react";
import * as Sections from "./Sections.js";
import DebuggerButton from "./DebuggerButton.js";
import classNames from "classnames";
import type { Node } from "react";
import "./Base.css";
import "./App.css";

window.stickyDebugger = require("./sticky-debugger.js").default;
console.log(window.stickyDebugger);

type Section =
  | "intro"
  | "problem"
  | "position_fixed"
  | "position_fixed_sad"
  | "sticky"
  | "rolodex"
  | "rolodex_sticky"
  | "overflow_hidden"
  | "js_saves_the_day"
  | "wtf_offset"
  | "wtf_hidden"
  | "wtf_height"
  | "working"
  | "wtf_unsupported_elements"
  | "wrapup";

type State = {|
  debug: boolean,
  section: Section
|};

const sections: Array<[Section, string]> = [
  ["intro", "Intro"],
  ["problem", "The Problem"],
  ["position_fixed", "Position: Fixed"],
  ["position_fixed_sad", "Position: Fixed :("],
  ["sticky", "Sticky"],
  // ["rolodex", "Rolodex"],
  ["rolodex_sticky", "Rolodexen"],
  // ["overflow_hidden", "Hidden Horrors"],
  // ["js_saves_the_day", "JavaScript"],
  ["wtf_offset", "Attempt 1"],
  ["wtf_height", "Attempt 2"],
  ["wtf_hidden", "Attempt 3"],
  ["working", "Attempt 4"],
  ["wrapup", "Wrap-Up"]
];

class App extends Component<{}, State> {
  state: State = {
    debug: false,
    section: "intro"
  };

  componentDidMount() {
    document.addEventListener("keyup", (event: KeyboardEvent) => {
      if (event.key !== "ArrowLeft" && event.key !== "ArrowRight") {
        return;
      }
      event.preventDefault();
      const diff = event.key === "ArrowLeft" ? -1 : 1;
      const currentSection = this.state.section;
      const currentSectionIndex = sections.findIndex(
        ([key, _]) => key === currentSection
      );
      const nextSection = sections[currentSectionIndex + diff];
      if (nextSection) {
        this.setState({ section: nextSection[0] }, () => {
          window.scrollTop = 0;
        });
      } else {
        console.error("Next section not found");
      }
    });
  }

  onChangeDebugMode = (event: SyntheticInputEvent<HTMLInputElement>): void => {
    this.setState({ debug: event.currentTarget.checked });
  };

  selectSection = (section: Section): void => {
    this.setState({ section });
  };

  renderSection(): Node {
    switch (this.state.section) {
      case "intro":
        return <Sections.Intro />;
      case "problem":
        return <Sections.Problem />;
      case "position_fixed":
        return <Sections.PositionFixed />;
      case "position_fixed_sad":
        return <Sections.PositionFixedSad />;
      case "sticky":
        return <Sections.PositionSticky />;
      case "rolodex":
        return <Sections.RolodexFullPage />;
      case "rolodex_sticky":
        return <Sections.RolodexMultiple />;
      // case "overflow_hidden":
      //   return <Sections.HiddenHorrors />;
      case "wtf_offset":
        return <Sections.WtfOffset />;
      case "wtf_height":
        return <Sections.WtfSameHeight />;
      case "wtf_hidden":
        return <Sections.WtfHidden />;
      case "working":
        return <Sections.Working />;
      case "wrapup":
        return <Sections.Wrapup />;
    }
    return "Section not found";
  }

  render() {
    const currentSection = this.state.section;
    const currentSectionIndex = sections.findIndex(
      ([key, _]) => key === currentSection
    );
    const style =
      currentSection === "wtf_hidden" ? { overflowX: "hidden" } : {};
    return (
      <div id="DublinJSDemoApp" className="App" style={style}>
        <div className="Sidebar">
          <div className="SidebarChild">
            <ul className="Menu">
              {sections.map(([key, title], index) => (
                <li
                  className={classNames({
                    past: index < currentSectionIndex,
                    active: currentSection === key,
                    next: index === currentSectionIndex + 1,
                    future: index > currentSectionIndex + 1
                  })}
                  onClick={() => this.selectSection(key)}
                >
                  {title}
                </li>
              ))}
              {/* <li>Find it!</li>
            <li>
              <label>
                <input
                  checked={this.state.debug}
                  type="checkbox"
                  onChange={this.onChangeDebugMode}
                />{" "}
                Debug Mode
              </label>
            </li> */}
            </ul>
            <DebuggerButton />
          </div>
        </div>
        <div className="Content">
          {/* <Reviewer name="Lauren O'Sullivan" sticky={true} />
          <Reviewer name="Jimmy Johnson" sticky={true} /> */}
          {this.renderSection()}
        </div>
      </div>
    );
  }
}

export default App;
