// @flow

import React, { Component } from "react";
import names from "./names.js";
import "./App.css";

export default class Rolodex extends Component<{}> {
  render() {
    const byLetter: { [string]: Array<string> } = {};
    names.forEach(name => {
      const firstLetter = name[0];
      if (!byLetter[firstLetter]) {
        byLetter[firstLetter] = [];
      }
      byLetter[firstLetter].push(name);
    });

    return (
      <div className="Rolodex">
        {Object.keys(byLetter).map((letter: string) => (
          <dl key={letter}>
            <dt>
              <div className="Rolodex-Letter">{letter}</div>
              <div className="Rolodex-Count">
                {byLetter[letter].length} Contacts
              </div>
            </dt>
            {byLetter[letter].slice(0, 15).map(name => (
              <dd>
                <span role="img" aria-label="Phone emoji">
                  📞
                </span>{" "}
                {name}
              </dd>
            ))}
          </dl>
        ))}
      </div>
    );
  }
}
