/* eslint-disable default-case */
// @flow

import React, { Component } from "react";
import type { Node } from "react";
import stickyDebugger from "./sticky-debugger.js";
import "./DebuggerButton.css";
import clippyImg from "./images/clippy.png";

type State = { issues: Array<[string, Element]> };

export default class DebuggerButton extends Component<{}, State> {
  state: State = { issues: [] };

  _onClick = () => {
    const elements = document.getElementsByClassName("StickyDebug");
    if (elements.length !== 1) {
      alert(`Found unexpected number of sticky elements: ${elements.length}`);
      return;
    }

    const issues = stickyDebugger(elements[0]);
    this.setState({ issues });
  };

  _onClickIssues = () => {
    this.setState({ issues: [] });
  };

  renderIssue = ([text, element]: [string, Element], i: number) => {
    const backgroundColor = window.getComputedStyle(element)[
      "background-color"
    ];
    return (
      <div className="DebuggerButton-Issue" key={i}>
        <div className="DebuggerButton-Issue-Icon" style={{ backgroundColor }}>
          &nbsp;
        </div>
        <div className="DebuggerButton-Issue-Text">{text}</div>
      </div>
    );
  };

  render(): Node {
    const issues = this.state.issues;
    return (
      <div>
        <div className="DebuggerButton">
          <div
            className="DebuggerButton-Icon"
            onClick={this._onClick}
            role="img"
            alt="Debugging Beetle"
          >
            🐞
          </div>
        </div>
        {issues.length > 0 && (
          <div className="DebuggerButton-Issues" onClick={this._onClickIssues}>
            <img src={clippyImg} />
            <div className="DebuggerButton-Issues-List">
              {issues.map((issue, i) => this.renderIssue(issue, i))}
            </div>
          </div>
        )}
      </div>
    );
  }
}
